
body,
html,
#db {
  margin: 0;
  padding: 0;
  height: 100vh;
}
#db {
  font-family: Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 1rem;
  padding: 0;
}
fieldset {
  border: 0;
  margin: 0 0 1rem;
  padding: 1rem 0;
}
label,
input {
  display: block;
  width: 92%;
}
input {
  font-size: 1.25rem;
  padding: 0.5rem 4%;
}
label {
  margin: 0 0 1rem;
}
.grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}
.grid__col--3 {
  -webkit-box-ordinal-group: 2;
      -ms-flex-order: 1;
          order: 1;
  width: 100%;
}
.grid__col--9 {
  -webkit-box-ordinal-group: 3;
      -ms-flex-order: 2;
          order: 2;
  width: 100%;
}
@media (min-width: 625px) {
.grid {
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row nowrap;
            flex-flow: row nowrap;
}
[class*="grid__col--"] {
    height: 100%;
}
.grid__col--3 {
    -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
            order: 2;
    padding: 0 1rem;
    width: 25%;
    border-left: 1px solid rgba(0, 0, 0, 0.125);
}
.grid__col--9 {
    -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
            order: 1;
    width: 75%;
}
[class*="grid__col--"]:last-child,
  [class*="grid__col--"]:only-child {
    margin-right: 0;
}
}



